import React, {useEffect, useState} from "react"
import classes from "./ModalLogIn.module.css"
import {useFormik} from "formik";
import {InputText} from "primereact/inputtext";
import {Password} from "primereact/password";
import {Checkbox} from "primereact/checkbox";
import {Button} from "primereact/button";
import showIco from "./icons/show.svg"
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import {getAuthUserData, login} from "../../redux/auth-reducer";

const ModalLogIn = (props) =>{
    const [disabledBtn, setDisabledBtn] = useState(true)
    const [showPassword, setShowPassword] = useState(false)
    /*useEffect(()=>{
        props.getAuthUserData()
    },[])*/
    const validate = values =>{
        const errors = {}
        if (!values.email) {
            errors.email = 'Ð—Ð°Ð¿Ð¾Ð»Ð½Ð¸Ñ‚Ðµ Ð¿Ð¾Ð»Ðµ';
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'ÐÐµÐ¿Ñ€Ð°Ð²Ð¸Ð»ÑŒÐ½Ñ‹Ð¹ email';
        }
        if (!values.password) {
            errors.password = 'Ð—Ð°Ð¿Ð¾Ð»Ð½Ð¸Ñ‚Ðµ Ð¿Ð¾Ð»Ðµ';
        }else if(values.password.length < 6){
            errors.password = 'ÐŸÐ°Ñ€Ð¾Ð»ÑŒ Ð´Ð¾Ð»Ð¶ÐµÐ½ Ð±Ñ‹Ñ‚ÑŒ Ð¾Ñ‚ 6 ÑÐ¸Ð¼Ð²Ð¾Ð»Ð¾Ð²'
        }

        let dis = values.email.length !== 0 && values.password.length !== 0 && Object.keys(errors).length == 0
        setDisabledBtn(!dis)

        return errors
    }

    const formik = useFormik({
        initialValues: {
            email:'',
            password:'',
            saveData: false
        },
        validate,
        validateOnBlur: true,
        onSubmit: values => {
            props.login(values.email, values.password)
        }
    })
    if(props.isAuth){
        props.closeModal()
    }

    return(
        <div className={classes.inner}>
            <h2 className ={classes.titleAuth}>Ð”Ð¾Ð±Ñ€Ð¾ Ð¿Ð¾Ð¶Ð°Ð»Ð¾Ð²Ð°Ñ‚ÑŒ!</h2>
            <form onSubmit={formik.handleSubmit}>
                <div className="p-fluid">
                    <div className="p-field">
                        <label htmlFor="email" className={classes.titleForms}>Email</label>
                        <InputText id="email" type="text"
                                   className={formik.errors.email ? "p-invalid": ''}
                                   onChange={formik.handleChange}
                                   onBlur={formik.handleBlur}
                                   value={formik.values.email}
                        />
                        {formik.errors.email ?  <small id="username2-help" className="p-invalid p-d-block">{formik.errors.email}!</small> : null}
                    </div>
                    <div className={`p-field`}>
                        <label htmlFor="password" className={classes.titleForms}>ÐŸÐ°Ñ€Ð¾Ð»ÑŒ</label>
                        <Password id="password"
                                  className={formik.errors.password ? "p-invalid": ''}
                                  onChange={formik.handleChange}
                                  onBlur={formik.handleBlur}
                                  value={formik.values.password}
                        />
                        <div className={classes.showPassword} onClick={()=>{
                            setShowPassword(!showPassword)
                            if(showPassword){
                                document.querySelector('#password').type = 'text'
                            }else{
                                document.querySelector('#password').type = 'password'
                            }
                        }}><img src={showIco} alt="show"/></div>
                        {formik.errors.password ?  <small id="username2-help" className="p-invalid p-d-block">{formik.errors.password}!</small> : null}
                    </div>
                </div>
                <Link className={classes.passForgot} href="#">Ð—Ð°Ð±Ñ‹Ð»Ð¸ Ð¿Ð°Ñ€Ð¾Ð»ÑŒ?</Link>
                <div className={classes.saveDataAndBtnInner}>
                    <div>
                        <Checkbox className={classes.checkbox} id="saveData" checked={formik.values.saveData} onChange={formik.handleChange}></Checkbox>
                        <label htmlFor="saveData" className="p-checkbox-label">Ð—Ð°Ð¿Ð¾Ð¼Ð½Ð¸Ñ‚ÑŒ Ð´Ð°Ð½Ð½Ñ‹Ðµ Ð´Ð»Ñ Ð²Ñ…Ð¾Ð´Ð°</label>
                    </div>
                    <Button type="submit" label="Ð’Ð¾Ð¹Ñ‚Ð¸" disabled={disabledBtn} className={`p-button-rounded ${classes.btn} ${classes.btnLogin}`} />
                </div>
            </form>
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    authorization: state.auth.authorization,
})

export default connect(mapStateToProps, {getAuthUserData, login})(ModalLogIn);